<template>
  <div>
    <VerticalNav v-bind:vertical="vertical" />
    <!-- Page content holder -->
    <div class="page-content p-5" id="content" v-bind:class="state" >
      <!-- Toggle button -->
      <button
        id="sidebarCollapse"
        type="button"
        class="btn btn-light bg-white rounded-pill shadow-sm px-4 mb-4"
        v-on:click="showVertical"
      >
        <i class="fa fa-bars"></i
        >
      </button>

      <router-view />
   
    </div>
  </div>
</template>

<script>
import VerticalNav from "@/components/VerticalNav.vue";
import Inicio from "@/sections/Inicio.vue"
import { mapState } from 'vuex';
export default {
  name: "Menu",
  data() {
    return {
      vertical: true,
      state: ''
    };
  },
  components: {
    VerticalNav,
    Inicio
  },
  methods: {
    showVertical() {
      this.vertical = !this.vertical;
    }
  },
  watch: {
    vertical(oldVal, newVal) {
      this.state = newVal ? "active" : "";
    }
  },
  computed: {
      ...mapState(['data_user'])
  }

};
</script>

<style>
body {
  background: #599fd9;
  background: -webkit-linear-gradient(to right, #599fd9, #c2e59c);
  background: linear-gradient(to right, #599fd9, #c2e59c);
  min-height: 100vh;
  overflow-x: hidden;
}

.separator {
  margin: 3rem 0;
  border-bottom: 1px dashed #fff;
}

.text-uppercase {
  letter-spacing: 0.1em;
}

.text-gray {
  color: #aaa;
}

#sidebar.active {
  margin-left: -17rem;
}

#content.active {
  width: 100%;
  margin: 0;
}

.page-content {
  width: calc(100% - 17rem);
  margin-left: 17rem;
  transition: all 0.4s;
}
@media (max-width: 768px) {
  #sidebar {
    margin-left: -17rem;
  }
  #sidebar.active {
    margin-left: 0;
  }
  #content {
    width: 100%;
    margin: 0;
  }
  #content.active {
    margin-left: 17rem;
    width: calc(100% - 17rem);
  }
}
</style>



