import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

const data_user_url = "http://ironspider.pythonanywhere.com/api/mydata/"
const data_robots_url =  "http://ironspider.pythonanywhere.com/api/robots/"
export default new Vuex.Store({
  state: {
    globalToken: false,
    data: null,

    data_user: {},
    data_robots: {},

  },
  mutations: {
    setGlobalToken(state, token){
      state.globalToken = token;
    },
    signOff(state){
      state.globalToken = false
    },
    setData(state, all){
      state.data = all;
    },
    updateDataUser(state, payload){
      state.data_user = payload
    },
    updateDataRobots(state, payload){
      state.data_robots = payload
    }
  },
  actions: {
    getDataUser(context){
      return new Promise((resolve,reject) =>{
        axios.get(data_user_url,{
          headers: {
            Authorization: 'Bearer ' + this.state.globalToken
          }
        }).then(result =>{
          console.log(result)
          context.commit('updateDataUser', result.data)
          resolve(result.data)
        })
      })
    
    },
    getDataRobots(context){
      return new Promise((resolve,reject) =>{
        axios.get(data_robots_url,{
          headers: {
            Authorization: 'Bearer ' + this.state.globalToken
          }
        }).then(result =>{
          console.log(result.data)
          context.commit('updateDataRobots', result.data)
          resolve(result.data)
        })
      })
    
    },
  },
  modules: {
  }
})



