<template>
  <b-container  >
    <b-row align-h="center" class="mt-5" align-v="center" >
      <b-col cols="12"  sm="12" md="6" lg="4" >
        <b-card
         
          title="Inicia Sesion"
          sub-title="veras que es divertido..."
        >
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-card-text>
              <b-form-group
                id="input-group-1"
                label="Usuario:"
                label-for="input-1"
              >
                <b-form-input
                  id="input-1"
                  v-model="form.username"
                  type="text"
                  required
                  placeholder="Nombre de usuario"
                ></b-form-input>
              </b-form-group>

              <b-form-group
                id="input-group-2"
                label="Contraseña:"
                label-for="input-2"
              >
                <b-form-input
                  id="input-2"
                  type="password"
                  v-model="form.password"
                  required
                  placeholder="Tu contraseña"
                ></b-form-input>
              </b-form-group>

              <p class="text-danger"  v-if="error" >Algo salió mal, intentalo de nuevo</p>
            </b-card-text>

            <b-button type="submit" variant="primary">Ingresar</b-button>
            <b-button type="reset" variant="danger">Reestablecer</b-button>
          </b-form>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import qs from "qs";
import axios from "axios";
import { mapMutations, mapActions } from 'vuex'

export default {
  name: "Login",
  data() {
    return {
      form: {
        username: "",
        password: "",
        
      },
      show: true,
      error: false,
    };
  },
  methods: {
    ...mapMutations(
      ['setGlobalToken', 'setData']
    ),
    ...mapActions([
      'getDataUser'
    ]),
    onSubmit(evt) {
      evt.preventDefault();
      const url = "http://ironspider.pythonanywhere.com/o/token/";
      const requestBody = {
        grant_type: "password",
        username: this.form.username,
        password: this.form.password,
        client_id: "1cIffmAYfd7f98cvVlyFhPqJthNuoaYZFpGAJJJ9",
        client_secret:
          "nPvA6L7h25zQwxLFq90bKGm2dM8VGTxrZGx98QbJqehmdx1OmW9fRqcD0s0e0VnJzbSmyauel5w7uTnSLDrb9zxq9jz2kwJor3QN0OJA0KdQPNY5qZiZsSDMMzQZQHoj"
      };
      const config = {
        headers: {
          "content-type": "application/x-www-form-urlencoded"
        }
      };
      axios.post(url, qs.stringify(requestBody), config).then(result=>{
          this.error = false
          this.setGlobalToken(result.data.access_token)
          this.setData(result.data)
          this.$router.push('menu')
          
          
          
      }).catch(e => {
        if(e.response){
          this.error = true
          console.log("ejecutando error")
        }
      });
    },

    
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.username = "";
      this.form.password = "";
    }
  }
};
</script>

<style scoped>
  
</style>



