<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="warning" class="nav-bk5 hor-nav">
      <b-navbar-brand href="" :to="{ name: 'home' }" exact>
        <img
          src="https://restream.io/blog/content/images/2019/10/rs-logo.png"
          width="30"
          height="30"
          alt="Logo"
        />
        IronRobots
      </b-navbar-brand>
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav>
          <b-nav-item v-if="globalToken"  :to="{ name: 'menu' }" exact>
            <span class="fa fa-home  fa-lg" ></span>
            Mi menu
          </b-nav-item>
          <b-nav-item :to="{ name: 'nosotros' }" exact>
            <span class="fa fa-group  fa-lg"></span>
            Nosotros
          </b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav v-if="!globalToken" is-nav class="ml-auto">
          <b-nav-item :to="{ name: 'login' }" exact>
            <b-button variant="danger" size="sm" >Iniciar Sesion</b-button>
          </b-nav-item>
          <b-nav-item :to="{ name: 'register' }" exact>
            <b-button variant="success" size="sm">Registrarse</b-button>
          </b-nav-item>
        </b-navbar-nav>
         <b-navbar-nav v-if="globalToken" is-nav class="ml-auto" v-on:click="signOff" >
          <b-nav-item :to="{ name: 'login' }" exact>
            <b-button variant="warning" size="sm" >Cerrar Sesion</b-button>
          </b-nav-item>
       
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
  name: "Navbar",
  data() {
    return {
      
    };
  },
  computed:{
    ...mapState(['globalToken'])
  },
  methods: {
    ...mapMutations(['signOff'])
  }
};
</script>

<style scoped>
.nav-bk {
  background: #40e0d0;
  background: -webkit-linear-gradient(to right, #ff0080, #ff8c00, #40e0d0);
  background: linear-gradient(to right, #ff0080, #ff8c00, #40e0d0);
}

.nav-bk2 {
  background: #23074d;
  background: -webkit-linear-gradient(to right, #cc5333, #23074d);
  background: linear-gradient(to right, #cc5333, #23074d);
}

.nav-bk3 {
  background: #00f260;
  background: -webkit-linear-gradient(to right, #0575e6, #00f260);
  background: linear-gradient(to right, #0575e6, #00f260);
}

.nav-bk4 {
  background: #070000;
  background: -webkit-linear-gradient(to right, #070000, #4c0001, #070000);
  background: linear-gradient(to right, #070000, #4c0001, #070000);
}

.nav-bk5 {
  background: #0575e6;
  background: -webkit-linear-gradient(to right, #021b79, #0575e6);
  background: linear-gradient(to right, #021b79, #0575e6);
}

.nav-bk6 {
  background: #2196f3;
  background: -webkit-linear-gradient(to right, #f44336, #2196f3);
  background: linear-gradient(to right, #f44336, #2196f3);
}

.nav-bk7 {
  background: #c2e59c;
  background: -webkit-linear-gradient(to right, #64b3f4, #c2e59c);
  background: linear-gradient(to right, #64b3f4, #c2e59c);
}

.nav-bk8 {
  background: #5f2c82;
  background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);
  background: linear-gradient(to right, #49a09d, #5f2c82);
}

.hor-nav {

  z-index: 100
 
}
</style>



