<template>
<div class="vertical-nav bg-white" id="sidebar" v-bind:class="state" >
  <div class="py-4 px-3 mb-4 bg-light mt-5">
    <div class="media d-flex align-items-center mt-3"><img src="https://image.flaticon.com/icons/png/512/149/149071.png" alt="..." width="65" class="mr-3 rounded-circle img-thumbnail shadow-sm">
      <div class="media-body">
        <h4 class="m-0">{{ data_user.username }}</h4>
        <p class="font-weight-light text-muted mb-0">{{  data_user.email}}</p>
      </div>
    </div>
  </div>

  <p class="text-gray font-weight-bold text-uppercase px-3 small pb-4 mb-0">Principal</p>

  <ul class="nav flex-column bg-white mb-0">
    <li class="nav-item">
     
                

            <router-link to="/menu/inicio"   exact
            class="nav-link text-dark font-italic bg-light">
            
            <i class="fa fa-th-large mr-3 text-primary fa-fw"></i>
                Inicio
           
            </router-link>
    </li>
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-address-card mr-3 text-primary fa-fw"></i>
                Sobre ti
            </a>
    </li>
    <li class="nav-item">



            <router-link to="/menu/robots"   exact
            class="nav-link text-dark font-italic bg-light">
            
             <i class="fa fa-cubes mr-3 text-primary fa-fw"></i>
                Tus robots
           
            </router-link>
    </li>
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-picture-o mr-3 text-primary fa-fw"></i>
                Gallery
            </a>
    </li>
  </ul>

  <p class="text-gray font-weight-bold text-uppercase px-3 small py-4 mb-0">Estadisticas</p>

  <ul class="nav flex-column bg-white mb-0">
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-area-chart mr-3 text-primary fa-fw"></i>
                ThingSpeak
            </a>
    </li>
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-bar-chart mr-3 text-primary fa-fw"></i>
                Todos los robots
            </a>
    </li>
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-pie-chart mr-3 text-primary fa-fw"></i>
                Sensores
            </a>
    </li>
    <li class="nav-item">
      <a href="#" class="nav-link text-dark font-italic">
                <i class="fa fa-line-chart mr-3 text-primary fa-fw"></i>
                Pronto...
            </a>
    </li>
  </ul>
</div>

</template>

<script>
import { mapMutations, mapActions, mapState } from 'vuex'

export default {
    name: 'VerticalNav',
    data(){
        return{
            
           state: '' 
        }
    },
    props: ['vertical'],
    watch: {
        vertical(oldVal, newVal){
            this.state = newVal ? 'active' : ''
        }
    },
    methods:{
        
    },
    created(){
        this.$store.dispatch('getDataUser')
    },
    computed: {
        ...mapState(['data_user']),
    }
}
</script>

<style scoped>

.vertical-nav {
  min-width: 17rem;
  width: 17rem;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
}


/* for toggle behavior */

#sidebar.active {
  margin-left: -17rem;
}

#content.active {
  width: 100%;
  margin: 0;
}

@media (max-width: 768px) {
  #sidebar {
    margin-left: -17rem;
  }
  #sidebar.active {
    margin-left: 0;
  }
  #content {
    width: 100%;
    margin: 0;
  }
  #content.active {
    margin-left: 17rem;
    width: calc(100% - 17rem);
  }
}
</style>


