<template>
    <div>
        <b-row>
            <b-col cols="12" sm="12" md="12" v-for="item in data_user.robots"
                :key = "item.id">
   <b-card
                
                overlay
                img-src="https://picsum.photos/900/250/?image=3"
                img-alt="Card Image"
                text-variant="white"
                :title="item.name"
                :sub-title="item.robot_type"
                class="m-4"
                
            >
                <b-card-text>
                    {{ item.description }}
                </b-card-text>
                
       
            
            <b-button variant="success"  :to="'robot/' + item.id" >Controlame</b-button>
           
        
            </b-card>

            </b-col>
         
         </b-row>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: 'Robots',
    computed: {
      ...mapState(['data_user'])
    }
}
</script>


