<template>
  <div class="container">
    <!-- Portfolio Item Heading -->
    <h1 class="my-4">
      {{ thisrobot.name }}
      <small>{{ thisrobot.robot_type }}</small>
    </h1>

    <!-- Portfolio Item Row -->
    <div class="row">
      <div class="col-md-8">
        <img class="img-fluid" v-bind:src="thisrobot.image" alt="" />
      </div>

      <div class="col-md-4">
        <h3 class="my-3">Descripcion</h3>
        <p>{{ thisrobot.description }}</p>
        <h3 class="my-3">Detalles</h3>
        <ul>
          <li>ID: {{ thisrobot.id }}</li>
          <li>Fecha de ingreso: {{ thisrobot.date_joined }}</li>
        </ul>
      </div>
    </div>
    <!-- /.row -->

    <!-- Related Projects Row -->
    <h3 class="my-4">Botones de control</h3>

    <div class="row">
      <div class="col-4"></div>
      <div class=""></div>

      <a
        class="arrow arrow-up"
        @click="sendDirection('Front')"
        title="Previous"
        href="javascript:;"
      ></a>
      <a
        class="arrow arrow-down"
        @click="sendDirection('Back')"
        title="Next"
        href="javascript:;"
      ></a>
      <a
        class="arrow arrow-right"
        @click="sendDirection('Right')"
        title="Right"
        href="javascript:;"
      ></a>
      <a
        class="arrow arrow-left"
        @click="sendDirection('Left')"
        title="Left"
        href="javascript:;"
      ></a>
            <a
        class="btn btn-danger"
        @click="sendDirection('Stop')"
        title="Parar"
        href="javascript:;"
      ><h1 class="mt-4" >Parar</h1></a>
    </div>
    <!-- /.row -->
  </div>
  <!-- /.container -->
</template>

<script>
import axios from "axios";
import { mapMutations, mapActions, mapState } from "vuex";

const movements_url =
  "http://ironspider.pythonanywhere.com/api/currentmovements/";

export default {
  name: "RobotDetail",
  methods: {
    ...mapActions(["getDataRobots"]),
    sendDirection(directionReceived) {
      return new Promise((resolve, reject) => {
        axios
          .put(
            movements_url + this.$route.params.id + "/",
            {
              robot: this.$route.params.id,
              direction: directionReceived
            },
            {
              headers: {
                Authorization: "Bearer " + this.globalToken,
                "Content-Type": "application/json"
              }
            }
          )
          .then(r => {
            console.log(r.data);
            resolve(r.data);
          });
      });
    }
  },
  created() {
    this.$store.dispatch("getDataRobots");
  },
  computed: {
    ...mapState(["data_robots", "globalToken"]),
    thisrobot() {
      return this.data_robots.find(r => r.id == this.$route.params.id);
    }
  }
};
</script>

<style scoped>
.arrow {
  position: relative;
  padding: 3rem; /* Arrow size */
  box-shadow: 1px -1px 0 1px rgb(243, 3, 3) inset;
  -webkit-box-shadow: 2px -2px rgb(243, 3, 3) inset;
  border: solid transparent;
  border-width: 0 0 2rem 2rem;
  transition: 0.2s;
}

.arrow:hover {
  box-shadow: 2px -2px 0 2px rgb(218, 193, 112) inset;
  -webkit-box-shadow: 4px -4px rgb(218, 193, 112) inset;
}

.arrow-up {
  transform: rotate(135deg);
}
.arrow-right {
  transform: rotate(225deg);
}
.arrow-down {
  transform: rotate(-45deg);
}
.arrow-left {
  transform: rotate(45deg);
}

body {
  background-color: lavender;
}

/* a:nth-child(5) {
  left: 100%;
  
} */
</style>



